import { Component } from '@angular/core';
import { WindowViewLayerContainerComponent } from '../../../';
import { WindowViewService } from '../../../';

@Component({
  moduleId: module.id,
  selector: 'simple-window',
  templateUrl: 'floating-window.component.html',
  directives: [WindowViewLayerContainerComponent]
})
export class FloatingWindowComponent {
  static WindowCount: number = 0;

  constructor(private windowView: WindowViewService) {
    this.title = `Floating Window ${FloatingWindowComponent.WindowCount}`;
    FloatingWindowComponent.WindowCount++;
  }

  title: string;

  closeWindow() {
    this.windowView.removeWindowByInstance(this);
  }
}